// RWD Bases
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin      : 0;
  font-family : sans-serif;
}

// Imgenes responsive
img {
  max-width : 100%;
  height    : auto;
}

a {
  text-decoration : none;

  &:hover { 
		text-decoration : none;
		color: var(--color-first);
	}
}
